import gql from 'graphql-tag'

export const REGISTER = gql`query Register($username: String!, $password: String!) {
    register(username: $username, password: $password) {
        id,
        username
    }
}`;

export const LOG_IN = gql`query Login($username: String!, $password: String!) {
    login(username: $username, password: $password)      
}`;

export const USERNAME_EXISTS = gql`query Exist($username: String!){
    usernameExists(username: $username)      
}`;

export const CREATE_ROOM = gql`mutation Create($name: String!){
    createRoom(name: $name){
        name
        owner{
            id
        }
    }      
}`;

export const UPDATE_ROOM = gql`mutation Update($id: ID!, $name: String!){
    updateRoom(id: $id, name: $name){
        name
    }      
}`;

export const DELETE_ROOM = gql`mutation Delete($id: ID!){
    deleteRoom(id: $id){
        id
        name
    }      
}`;

export const GET_ALL_CHATS = gql`query Rooms{
    rooms {
        id
        name
        owner{
            id
        }
    }   
}`;

export const ME = gql`query Me{
    me{
        id
    }
}`;

export const ROOM_CREATED = gql`subscription RoomCreated{
    roomCreated {
        id
        name
        owner{
            id
        }
    }   
}`;

export const ROOM_UPDATED = gql`subscription RoomUpdated{
    roomUpdated {
        id
        name
        owner{
            id
        }
    }   
}`;

export const ROOM_DELETED = gql`subscription RoomDeleted{
    roomDeleted {
        id
        name
        owner{
            id
        }
    }   
}`;

export const MESSAGE_CREATED = gql`subscription MessageCreated{
    messageCreated {
        id
        text
        timestamp
        room{
            id
        }
        author{
            id
            username
        }
    }   
}`;

export const CURRENT_ROOM_CHANGED = gql`subscription CurrentRoomChanhed{
    currentRoomChanged {
        id
        username
    }   
}`;

export const MEMBER_JOINED = gql`subscription MemberJoined{
    memberJoined {
        id
        username
    }   
}`;

export const MEMBER_LEFT = gql`subscription MemberLeft{
    memberLeft {
        id
    }   
}`;

export const JOIN_ROOM = gql`mutation Join($id: ID!){
    joinRoom(roomId: $id) {
        id
        name
        owner{
            id
        }
        members{
            username
            id
        }
        lastMessages{
            text
            timestamp
            author{
              username
              id
            }
        }
        
    }   
}`;

export const EXIT = gql`mutation Exit{
    leaveCurrentRoom{
      name
    }
  }`;

export const SEND_MESSAGE=gql`mutation Send($message: String!){
    createMessage(text: $message){
        id
        text
    }
}`;






