<template>
  <div class="login">
    <h1>Login</h1>
    <ul id="form">
      <li><input v-model="username" placeholder="Username" /></li>
      <li>
        <input v-model="password" placeholder="Password" type="password" />
      </li>
      <li><button v-if="!isLogged" @click="logIn">Login</button></li>
      <li><button v-if="isLogged" @click="logout">Logout</button></li>
    </ul>
    <LogImage v-if="isLogged" />
    <ModalRegister v-bind:msg="message" />
  </div>
</template>

<script>
// @ is an alias to /src
import ModalRegister from "@/components/ModalRegister.vue";
import LogImage from "@/components/Logged.vue";
import { LOG_IN } from "./../constants/graphql.js";

export default {
  name: "Login",
  components: {
    ModalRegister,
    LogImage
  },
  props: ['is-logged'],
  data() {
    return {
      username: "",
      password: "",
      message: "",
      componentName: "LogImage",
    };
  },
  apollo: {},
  methods: {
    clearFields() {
      this.username = "";
      this.password = "";
    },
    auth(token) {
      this.$store.dispatch("setToken", token);
      localStorage.setItem("token", token);
      console.log(this.$store.getters.TOKEN);
    },
    logout() {
      this.$store.dispatch("setToken", "");
      localStorage.removeItem('token');
      this.$emit('logout');
      console.log(this.$store.getters.TOKEN);
    },
    async logIn() {
      if (this.password !== "" && this.username !== "") {
        try {
          const result = await this.$apollo.query({
            query: LOG_IN,
            variables: {
              username: this.username,
              password: this.password
            }
          });
          const token = String(result.data.login);
          this.auth(token);
          this.$emit('login');
          this.clearFields();
        } catch (err) {
          this.message = "Wrong credentials. Try again." + err;
          this.$modal.show("notif");
          this.clearFields();
        }
      } else {
        this.message = "All fields are required";
        this.$modal.show("notif");
        this.clearFields();
      }
    }
  }
};
</script>

<style>
#form {
  list-style-type: none;
}
.vm--modal {
  height: max-content;
  width: max-content;
}
</style>
